<template>

<div id="input">

  <h1>To Do List</h1>

  <form onsubmit="return false">
    <input id="list" v-model="newTodo" v-on:keypress.enter="addTodo" />
    <button @click="addTodo">Add To do!</button>
  </form>

  <div v-for="(todo, index) in todos" :key="todo.id">
    <input type="checkbox" v-model="todo.completed">
      <span :class="{completed : todo.completed}"> {{todo.title}}</span>
    <button class="delete" @click="removeTodo(index)">&times;</button>
  </div>

  <form onsubmit="return false">
    <button v-show="selected" @click="deleteSel">Delete Selection</button>
  </form>
</div>
</template>



<script>

import {TweenMax} from 'gsap'

export default {

  data() {

    return {
      newTodo: '',
      idForTodo: 1,
      todos: []
    }
  },

  methods: {
    addTodo: function() {
      if (this.newTodo.length == 0) {
        return
      }

      this.todos.push({
        id: this.idForTodo,
        title: this.newTodo,
        completed: false,
      })

      this.newTodo = ''
      this.idForTodo++
    },

    removeTodo(index) {
      this.todos.splice(index,1)
      TweenMax.to("body", 1, {backgroundColor: "red", onComplete: function () {
          document.body.style.backgroundColor = "white";
      }});
    },

    deleteSel() {

    this.todos = this.todos.filter(todo => !todo.completed);

    }


  },

  computed: {
    selected() {
      return this.todos.filter(todo =>todo.completed).length
    }
  }
}
</script>

<style>
.delete {
  border: none;
  background-color: white;
}

.completed {
  text-decoration: line-through;
}
</style>
